.b-order-track {
  padding-top: 56px;
  padding-bottom: 100px;
  background-color: #f7f7f7;
  background-image: url("../img/utilization/1.png");
  background-repeat: no-repeat;
  background-position: 66% 57%;

  &--normative {
    background-image: none !important;
  }
}

.b-order-track__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.b-order-track__content {
  max-width: 560px;
  padding-bottom: 97px;
}

.b-order-track__title {
  margin-bottom: 97px;
}

.b-order-track__descr {
  max-width: 350px;
  font-family: $mainFont;
  font-size: 18px;
  color: $dark;
  line-height: 23px;
}

.b-order-track__button {
  margin-top: 135px;
}

.b-order-track__logo {
  margin-right: 180px;
  background: none;
}
